.headbar{
    /* background-color: black;
    color: white; */
    height: 100vh;
    width: 100%;
}

.content{
    font-weight: 600;
    font-size: larger;
    position: relative;
    top: 50%;
}

/* introduction styles */

.intro-img{
    width: 100%;
    height: auto;
}

.intro-img-pad{
    padding: 0 15%;
}

.intro-title-card{
    padding: 5% 5%;
    box-shadow: none;
}

/* services styles */

.service-padding{
    padding-top: 5%;
    padding-left: 5%;
    padding-right: 5%;
}

.service-padding-2{
    padding-top: 2%;
}

.modal-padding{
    padding-left: 20%;
    padding-right: 20%;
}

/* comp styles */

.comp-padding{
    padding-top: 5%;
}

.comp-background{
    background-color: white;
}

/* campus styles */

.campus-style{
    background: url(./img/campus_ambassador.svg);
    height: 120vh;
    margin-top: 5%;
}

/* connect style */

.connect-style{
    padding-top: 5%;
}

.connect-padding{
    padding: 3% 5%;
}

.connect-padding-2{
    padding: 0 5%;
}   
